<template>
  <div>
    <div class="google-map fill-height" ref="googleMap" />
    <va-modal v-model="createModal" no-outside-dismiss
      fullscreen
      hide-default-actions
      :title="$t('generic.Detailes') +' ' + routeName "
      :cancelText=" $t('modal.cancel')">
      <div v-if="loading" class="flex-center spinner-box">
        <SpringSpinner
          :animation-duration="1500"
          :color="this.$themes.primary"
          :size="80"
        >
        </SpringSpinner>
      </div>
      <div  v-else>
        <div class="row justify--space-between">
          <span>الوقت المتوقع <span>{{expextedTime}}</span></span>
          <span>المسافة<span>{{distance}}</span></span>
          <!-- <span>{{timeNow - expextedTime >= 3 ? '' :''}}</span> -->
          <span>الوقت الان <span>{{timeNow}}</span></span>

        </div>
        <div class="row flex-center">

          <div id="map" class="google-map  fill-height" ref="googleMapDetails" style="height: 69vh;"/>
          <div id="sidebar">
            <div id="panel"></div>
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="createModal = false">
            {{ $t('modal.cancel') }}
          </va-button>
        </div>
      </div>
    </va-modal>
  </div>
</template>

<script>
import * as GoogleMapsLoader from 'google-maps'
import firebase from 'firebase'
// import { uniqWith, isEqual } from 'lodash'
import * as spinners from 'epic-spinners'
import axios from 'axios'

export default {
  name: 'google-map',
  data () {
    return {
      routeName: '',
      timeNow: '',
      distance: '',
      expextedTime: '',
      dataTable: [
        [24.903568262132232, 46.70643480252375],
        [24.760974344801873, 46.647011298295844],
        [24.727735687466648, 46.66380934941976],
        [24.742007579699624, 46.69780225105244],
        [24.740766564100955, 46.59803407968792],
        [24.722140458133055, 46.66648490098593],
        [24.816657580193898, 46.77706900525439],
        [24.794778003457576, 46.85968684908143],
      ],
      loading: true,
      createModal: false,
    }
  },
  components: {
    ...spinners,
  },
  async mounted () {
    const self = this
    GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
    /* We stick to version updates instead of weekly channel update because
       using weekly updates you need to test them, but we prefer to use stable
       versions. */
    GoogleMapsLoader.VERSION = '3.35'
    GoogleMapsLoader.load((google) => {
      /* eslint-disable no-new */
      const map = new google.maps.Map(this.$refs.googleMap, {
        center: new google.maps.LatLng(24.774265, 46.738586),
        zoom: 11,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      })

      // const trafficLayer = new google.maps.TrafficLayer()

      // trafficLayer.setMap(map)

      var directionsService = new google.maps.DirectionsService()
      function getPoly (path, obj, isbest, index) {
        var color = isbest ? '#1300e8' : (obj.iSPrimary ? '#46a28b' : '#ff1d5e')
        console.log(color)
        var arrayPath = google.maps.geometry.encoding.decodePath(path)
        var polylineOptionsActual = new google.maps.Polyline({
          path: arrayPath,
          strokeColor: color,
          geodesic: true,
          strokeOpacity: 0.6,
          strokeWeight: 6,
          zIndex: index,
        })

        polylineOptionsActual.setMap(map)

        google.maps.event.addListener(polylineOptionsActual, 'click', function (evt) {
          console.log('route')
          self.routeName = obj.RouteName
          self.onRouteClick(obj)
        })

        return polylineOptionsActual
      }
      function render (obj, index, org, destination, waypoints, isbest = false) {
        var rendererOptions = {
          preserveViewport: true,
          suppressMarkers: true,
          routeIndex: index,
          polylineOptions: new google.maps.Polyline({
            geodesic: true,
            strokeOpacity: 0,
            strokeWeight: 0.7,
          }),
          map,
        }
        var request = {
          origin: org,
          destination: destination,
          travelMode: google.maps.TravelMode.DRIVING,
          waypoints: waypoints,
        }
        var directionsDisplay = new google.maps.DirectionsRenderer(rendererOptions)
        directionsDisplay.setMap(map)
        directionsService.route(request, function (result, status) {
          new google.maps.Marker({
            position: new google.maps.LatLng(org),
            map: map,
          })
          if (status === google.maps.DirectionsStatus.OK) {
            // directionsDisplay.setDirections(result)
            getPoly(result.routes[0].overview_polyline, obj, isbest, index)
          }
        })
      }
      var routes = []
      const firestore = firebase.firestore()
      firestore.collection('routes').get().then(querySnapshot => {
        this.dataTable = []
        querySnapshot.forEach((doc, i) => {
          var obj = doc.data()
          obj.id = doc.id
          routes.push({
            org: obj.FromLocation,
            des: obj.toLocation,
          })
          var LongLat = obj.FromLocation.split(',')
          var org = new google.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
          var LongLatDes = obj.toLocation.split(',')
          var destination = new google.maps.LatLng(Number(LongLatDes[0]), Number(LongLatDes[1]))
          var waypoints = []
          obj.waypoints.forEach(point => {
            waypoints.push({
              location: { placeId: point.place_id },
            })
          })
          render(obj, i, org, destination, waypoints)
        })

        // routes = uniqWith(routes, isEqual)
        // routes.forEach((route, i) => {
        //   var LongLat = route.org.split(',')
        //   var org = new google.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        //   var LongLatDes = route.des.split(',')
        //   var destination = new google.maps.LatLng(Number(LongLatDes[0]), Number(LongLatDes[1]))
        //   var routeData = {
        //     RouteName: 'المسار الأفضل',
        //     toLocation: route.des,
        //     FromLocation: route.org,
        //     waypoints: [],
        //   }
        //   render(routeData, (i + 100), org, destination, [], true)
        // })
      })
      new google.maps.Marker({
        position: new google.maps.LatLng(new google.maps.LatLng(24.835745918122562, 46.72987580001744)),
        map: map,
        animation: google.maps.Animation.BOUNCE,
        icon: 'https://gcdnb.pbrd.co/images/hnKUay2TdxIv.png',
      })
      // this.dataTable.forEach(element => {
      //   new google.maps.Marker({
      //     position: new google.maps.LatLng(new google.maps.LatLng(element[0], element[1])),
      //     map: map,
      //     animation: google.maps.Animation.DROP,
      //     icon: 'https://i.ibb.co/27mScg4/ball1.png',
      //   })
      // })
    })
  },
  methods: {
    onRouteClick (route) {
      this.loading = true
      this.createModal = true

      var FromLocation = route.FromLocation.split(',')
      var toLocation = route.toLocation.split(',')
      var waypoints = []
      route.waypoints.forEach(point => {
        waypoints.push({ placeId: point.place_id })
      })
      this.expextedTime = route.DeafultTime ? (route.DeafultTime / 60).toFixed(2) + 'د' : ''
      const self = this

      var req = {
        origin: {
          location: {
            latLng: {
              latitude: Number(FromLocation[0]),
              longitude: Number(FromLocation[1]),
            },
          },
        },
        destination: {
          location: {
            latLng: {
              latitude: Number(toLocation[0]),
              longitude: Number(toLocation[1]),
            },
          },
        },
        intermediates: waypoints,
        travelMode: 'drive',
        routingPreference: 'traffic_aware',
        polylineQuality: 'high_quality',
        computeAlternativeRoutes: false,
        routeModifiers: {
          avoidTolls: false,
          avoidHighways: false,
          avoidFerries: false,
          avoidIndoor: false,
        },
        languageCode: 'ar-EG',
        units: 'METRIC',
      }
      axios.post('https://routes.googleapis.com/directions/v2:computeRoutes', req, {
        headers: {
          'Content-type': 'application/json',
          'X-Goog-Api-Key': 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs',
          'X-Goog-FieldMask': '*',
        },

      }).then(res => {
        this.loading = false

        if (res.data.routes[0].duration) {
          const time = Number(res.data.routes[0]?.duration?.slice(0, -1))
          self.timeNow = (time / 60).toFixed(2) + 'د'
        }
        if (this.expextedTime) {
          const time = Number(res.data.routes[0]?.staticDuration?.slice(0, -1))
          self.expextedTime = (time / 60).toFixed(2) + 'د'
        }
        self.distance = res.data.routes[0]?.distanceMeters / 1000 + 'كم'
        GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
        GoogleMapsLoader.VERSION = '3.35'
        GoogleMapsLoader.load((google) => {
          setTimeout(() => {
            const map = new google.maps.Map(this.$refs.googleMapDetails, {
              center: new google.maps.LatLng(24.774265, 46.738586),
              zoom: 11,
              mapTypeId: google.maps.MapTypeId.ROADMAP,
            })

            var colors = { TRAFFIC_JAM: '#d74342', SLOW: '#e69e01', NORMAL: '#4078c9' }

            var arrayPath = google.maps.geometry.encoding.decodePath(res.data.routes[0]?.polyline?.encodedPolyline)
            res.data.routes[0].travelAdvisory.speedReadingIntervals.forEach((step) => {
              const currentPath = arrayPath.slice(step.startPolylinePointIndex === 0 ? 0 : (step.startPolylinePointIndex - 1), step.endPolylinePointIndex)
              var polylineOptionsActual = new google.maps.Polyline({
                path: currentPath,
                strokeColor: colors[step.speed],
                geodesic: true,
                strokeOpacity: 1,
                strokeWeight: 6,
              })

              polylineOptionsActual.setMap(map)
            })
            const directionsService = new google.maps.DirectionsService()

            const display = new google.maps.DirectionsRenderer({
              preserveViewport: true,
              suppressMarkers: true,
              map,
              panel: document.getElementById('panel'),
            })
            var waypoints2 = []
            route.waypoints.forEach(point => {
              waypoints2.push({
                location: { placeId: point.place_id },
              })
            })
            directionsService
              .route({
                origin: new google.maps.LatLng(Number(FromLocation[0]), Number(FromLocation[1])),
                destination: new google.maps.LatLng(Number(toLocation[0]), Number(toLocation[1])),
                waypoints: waypoints2,
                travelMode: 'DRIVING',
                language: 'ar-EG',
              })
              .then((result) => {
                display.setDirections(result)
              })
            new google.maps.Marker({
              position: new google.maps.LatLng(Number(FromLocation[0]), Number(FromLocation[1])),
              map: map,
              animation: google.maps.Animation.BOUNCE,
            })
            new google.maps.Marker({
              position: new google.maps.LatLng(Number(toLocation[0]), Number(toLocation[1])),
              map: map,
              animation: google.maps.Animation.BOUNCE,
            })
          }, 100)
        })

        console.log(res)
      })
    },
  },
}
</script>

